\n\r\n      &:hover, &:focus\r\n        border-color: $color3\r\n\r\n    #canvas\r\n      @include border($border * 2, $color1)\r\n      background-color: white\r\n      margin: $margin\r\n\r\n    .canvasButtons\r\n      display: flex\r\n      align-content: space-around\r\n\r\n      button\r\n        margin-left: 0\r\n\r\n    #clearBtn\r\n      margin-left: auto\r\n      margin-right: 0\r\n\r\n\r\n\r\n\r\n\r\n  #colorBlocks\r\n    margin-right: auto\r\n    margin-left: auto\r\n    margin-top: $block_width * 0.5\r\n    width: $button_width\r\n    display: flex\r\n    flex-direction: column\r\n    align-items: center\r\n    align-content: space-around\r\n\r\n    .color\r\n      @include border($border, $color1)\r\n      border-radius: 40%\r\n      min-width: 90%\r\n      margin: $margin / 2.5\r\n\r\n      &:hover, &:focus\r\n        border-color: $color3\r\n\r\n\r\n\r\n    @each $color, $value in (color1: #EE2029, color2: #EE7B20, color3: #F5E52E, color4: #58B422, color5: #75E1DC, color6: #1C20E9, color7: #781CE9, color8: #808080)\r\n      .#{$color}\r\n        background-color: $value\r\n\r\n\r\n\r\n\r\n\r\n\r\n\r\n","@import \"../../pages/painting/painting-page\"\r\n\r\n#routerContainer\r\n  .appButtons\r\n    display: flex\r\n\r\n    #chooseImageBtn\r\n      margin-left: auto\r\n\r\n  button\r\n    a\r\n      text-decoration: none\r\n      color: $color1\r\n\r\n  #strokeWidth\r\n    margin-top: $block_width * 0.5\r\n\r\n    svg\r\n      margin: $margin\r\n      margin-top: 0\r\n\r\n      &:hover, &:focus\r\n        stroke: $color3\r\n","\r\n\r\n\r\n  .gallery\r\n    margin: $margin auto\r\n    padding: $padding\r\n    min-width: $image_width * 4\r\n    max-width: $image_width * 6\r\n    min-height: $image_width * 3\r\n    overflow: auto\r\n    display: flex\r\n    flex-wrap: wrap\r\n    align-items: stretch\r\n    justify-content: space-around\r\n    background-color: $color2\r\n    @include border($border, $color3)\r\n\r\n    figure\r\n      overflow: hidden\r\n 